import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import 'react-toastify/dist/ReactToastify.css';
import './CSS/font-awesome/css/font-awesome.min.css';
import './CSS/skins.min.css';
import './CSS/AdminLTE.min.css';
import './CSS/App.css';
import 'react-toastify/dist/ReactToastify.css';
import ListCotizaciones from './App/Cotizacion';
import ListFactura from './App/Facturas';
import ListNotas from './App/Nota';
import ListOrdenes from './App/Orden';
import ListConpra from './App/Compra';
import NuevaOrden from './App/Orden/Nuevo';
import NuevaCompra from './App/Compra/Nuevo';
import { ToastContainer } from 'react-toastify';
import NuevaNota from './App/Nota/Nuevo';
import NuevaFactura from './App/Facturas/Nuevo';
import NuevaCotizacion from './App/Cotizacion/Nueva';
import Header from './Template/Header';
import Menu from './Template/Menu';
import Footer from './Template/Footer';
import ListClientes from './App/Clientes';
import ListaProveedores from './App/Proveedores';
import ListaProductos from './App/Productos';
import Config from './App/Config';

export default class App extends React.Component {
  render() {
    let data = localStorage.getItem('menu');
    if (!data) {
      data = "open";
      localStorage.setItem('menu', 'open');
    }
    let styles = {};
    if (data === 'cerrado')
      styles = { marginLeft: "50px", minHeight: "calc(100vh - 110px)", };
    else
      styles = { marginLeft: "230px", minHeight: "calc(100vh - 110px)", };
    return (<Router>
      <Header update={this.forceUpdate.bind(this)} key='headerBar' />
      <Menu state={data} key='MenuAside' />
      <section style={styles} className="content">
        <ToastContainer position='bottom-right' />
        <Switch>
          <Route path="/ver_cotizaciones" component={ListCotizaciones} />
          <Route path="/nueva_cotizacion" component={NuevaCotizacion} />
          <Route path="/ver_ordenes" component={ListOrdenes} />
          <Route path="/nueva_orden" component={NuevaOrden} />
          <Route path="/ver_facturas" component={ListFactura} />
          <Route path="/nueva_factura" component={NuevaFactura} />
          <Route path="/ver_notas" component={ListNotas} />
          <Route path="/nueva_nota" component={NuevaNota} />
          <Route path="/ver_compras" component={ListConpra} />
          <Route path="/nueva_compra" component={NuevaCompra} />
          <Route path="/ver_proveedores" component={ListaProveedores} />
          <Route path="/ver_clientes" component={ListClientes} />
          <Route path="/ver_productos" component={ListaProductos} />
          <Route path={[
            "/conf_ventas",
            "/conf_region",
            "/conf_orden",
            "/conf_compra",
            "/conf_cotizacion",
            "/conf_nota",
            "/conf_factura",
            '/conf_ajustes',
            '/conf_reportes',
            "/conf_empresa",
            "/departamentos",
            "/tipo_producto",
            "/unidades",
            "/moneda",
            "/costos"
          ]} component={Config} />
        </Switch>
      </section>
      <Footer />
    </Router>);
  }
}

