import React from 'react'
import { Button, Col } from 'react-bootstrap';
import { conProducto, Producto } from '../../Modelos/Productos';
import SetText from '../../Utils/setTex';
import Tabla from '../../Utils/Table';
import { busqueda, busquedaFechas, filtro, ordenarPor } from '../../Utils/Utiles';
import DepartamentoSelector from '../Departamento/Selector';
import FilaProducto from './Fila';
import FormProductos from './FormProductos';

export default class ListaProductos extends Tabla {
    con = conProducto();
    placeholder = "BUSCAR PRODUCTO";
    departamento: string = '';
    max: number = 0;
    min: number = 0;
    buscarLabel: any[] = ['codigo', 'descripcion', 'contacto', 'correo', 'telefono'];
    title() {
        return <>
            <FormProductos
                open={this.state.modalProductos}
                close={() => { this.setState({ modalProductos: false }) }}
                add={this.set.bind(this)} />
            <div className="btn-group pull-right">
                <Button onClick={() => { this.setState({ modalProductos: true }) }} bsStyle='primary'>
                    <span className="glyphicon glyphicon-plus"></span> NUEVO PRODUCTO
                </Button>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> BUSCAR PRODUCTO</h4>
        </>
    }
    Avansado() {
        return <div className='row'>
            <Col style={{ minWidth: '585px', }} xs={7} className='RangoFechas' ><br />
                <div style={{ width: '100%' }} className="input-group">
                    <span style={{ width: '200px', position: 'relative', backgroundColor: "#eee" }} className="form-control">TIEMPO DE CREACION</span>
                    <input style={{ width: 'calc(50% - 120px)', position: 'relative' }} className="form-control" onChange={this.setFechaInicio.bind(this)} value={this.state.fecha_ini} name="fecha_ini" type="date" />
                    <input style={{ width: 'calc(50% - 120px)', position: 'relative' }} className="form-control" onChange={this.setFechaFin.bind(this)} value={this.state.fecha_fin} name="fecha_fin" type="date" />
                    <Button onClick={this.limpiarFechas.bind(this)} data-tip="LIMPIAR FECHAS" style={{ width: '40px', height: '34px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="danger"><i className="glyphicon glyphicon-ban-circle"></i></Button>
                </div>
            </Col>
            <Col style={{ minWidth: '435' }} xs={5} className='productoStock'><br />
                <div style={{ width: '100%' }} className="input-group">
                    <span style={{ width: '140px', position: 'relative', backgroundColor: "#eee" }} className="form-control">STOCK MINIMO</span>
                    <SetText style={{ width: 'calc(50% - 130px)' }} minValue={0} step='0.01' type="number" setData={this.setStock.bind(this)} value={this.min} update="min" />
                    <span style={{ width: '80px', position: 'relative', backgroundColor: "#eee" }} className="form-control">MAXIMO</span>
                    <SetText style={{ width: 'calc(50% - 130px)' }} minValue={0} step='0.01' type="number" setData={this.setStock.bind(this)} value={this.max} update="max" />
                    <Button onClick={this.setStock.bind(this, {})} data-tip="LIMPIAR STOCK" style={{ width: '40px', height: '34px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="danger"><i className="glyphicon glyphicon-ban-circle"></i></Button>
                </div>
            </Col>
            <Col data-tip="FILTRAR POR DEPARTAMENTO" xs={7} style={{ minWidth: '320px' }} className='RangoFechas'><br />
                <DepartamentoSelector
                    change={this.setDepartamento.bind(this)}
                    defaultValue={this.departamento} />
            </Col>
            <Col style={{ minWidth: '230px' }} xs={5} className='productoStock'><br />
                <select data-tip="FILTRAR POR EL ESTADO" onChange={this.setStatus.bind(this)} className="form-control" value={this.state.estatus}>
                    <option value={-1}>TODOS LOS ESTADOS</option>
                    <option value={0}>INACTIVO</option>
                    <option value={1}>ACTIVO</option>
                </select>
            </Col>
        </div >;
    }
    Row(d: Producto) {
        return <FilaProducto data={d} update={this.set.bind(this)} />
    }
    Header() {
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }}
                onClick={() => { this.setOrden("codigo") }}>
                RIF {this.flechas("codigo")}
            </th>
            <th style={{ width: 'calc(100% - 335px)', backgroundColor: this.colorOrden("descripcion") }}
                onClick={() => { this.setOrden("descripcion") }}>
                DESCRIPCION {this.flechas("descripcion")}
            </th>
            <th style={{ width: "100px" }}>CANTIDAD</th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "135px" }}>ACCIONES</th>
        </tr>
    }
    setStock(data: { max?: number, min?: number }) {
        if (data.max) this.max = data.max;
        if (data.min) this.min = data.min;
        if (data.min === data.max) {
            this.min = 0;
            this.max = 0;
        }
        this.forceUpdate();
        setTimeout(() => {
            console.log(data);
            console.log('max ' + this.max);
            console.log('min ' + this.min);

        }, 100);
    }
    setDepartamento(departamento: string) {
        this.departamento = departamento;
        this.forceUpdate();
    }
    filtros() {
        let { state: { itens, fecha_fin, fecha_ini, buscar, estatus, ordenar }, departamento, min, max } = this;
        if (itens !== 'carro') {
            itens = busqueda(itens, buscar, this.buscarLabel);
            itens = busquedaFechas(itens, fecha_ini, fecha_fin);
            itens = filtro(itens, estatus, 'estatus');
            itens = ordenarPor(itens, this.orden, ordenar);
            if (departamento !== '')
                itens = itens.filter((c: Producto) => {
                    if (c.departamento === departamento) return true;
                    return false;
                });
            itens = itens.filter(c => {
                if (c.cantidad >= min) return true;
                return false;
            });
            if (max !== 0)
                itens = itens.filter(c => {
                    if (c.cantidad <= max) return true;
                    return false;
                });
            this.length = itens.length;
            return itens;
        }
        return [];
    }

}
