import React from 'react';
import { conUsuario } from '../Modelos/Usuario';
interface State {
    id_usuario: number
    nivel: number;
    usuario: string
    open: boolean
}
interface Props {
    update: () => void
}
export default class Header extends React.Component<Props, State> {
    state = { id_usuario: 0, nivel: 0, usuario: '', open: false };
    render() {
        let { usuario, open } = this.state;
        return <header className="main-header">
            <a href="/admin" className="logo">
                <span className="logo-mini">
                    <img src="/logos/logo.svg" width="95%" alt='logo' />
                </span>
                <span className="logo-lg">
                    <img src="/logos/logo-text.svg" width="100%" alt='logo-text' />
                </span>
            </a>
            <nav style={{ height: "50px" }} className="navbar navbar-static-top" role="navigation">
                <div onClick={this.open.bind(this)} className="sidebar-toggle" data-toggle="push-menu" role="button">
                    <span className="sr-only">Toggle navigation</span>
                </div>
                <div className="navbar-custom-menu">
                    <ul className="nav navbar-nav">
                        <li className={"dropdown user user-menu" + ((open) ? ' open' : '')}>
                            <a href="#menu" onClick={() => { this.setState({ open: !open }) }} className="dropdown-toggle" data-toggle="dropdown" aria-expanded="true">
                                <span className="hidden-xs">{usuario}</span>
                            </a>
                            <ul className="dropdown-menu">
                                <li className="user-header">
                                    <p>
                                        {usuario}
                                    </p>
                                </li>
                                <li className="user-footer">
                                    <div className="pull-right">
                                        <a href="config/salir.php" className="btn btn-default btn-flat">Salir</a>
                                    </div>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </nav>
        </header>
    }

    open() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (data === 'open') {
            localStorage.setItem('menu', 'cerrado')
            data = 'cerrado'
        } else {
            localStorage.setItem('menu', 'open')
            data = 'open'
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
        this.props.update();
    }

    componentDidMount() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (!data) {
            data = "open";
            localStorage.setItem('menu', 'open')
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
        conUsuario().get('/api/Usuario/actual', this.setState.bind(this));
    }
}