import React from 'react';
import { Button, Panel } from 'react-bootstrap';
import Formulario from './Formulario'
import { ConConfig } from '../../../Modelos/Config';
import SetText from '../../../Utils/setTex';
import { Impreciones, imprecionNull } from '../../../Modelos/General';
interface State extends Impreciones {
    nota: string
}
export default class Compra extends React.Component<any, State> {
    con = ConConfig('compra');
    state = { ...imprecionNull, nota: ''};
    render() {
        let setState = this.setState.bind(this);
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE COMPRA </h4>
            </Panel.Heading>
            <Panel.Body>
                <Panel>
                    <Panel.Heading>
                        <h5>CONFIGURACION DE DATOS POR DEFECTO </h5>
                    </Panel.Heading>
                    <Panel.Body>
                        <b>NOTA</b>
                        <SetText rows={6} maxLength={600} type="textarea" value={this.state.nota} setData={setState} update="nota" />
                    </Panel.Body>
                </Panel>
                <Formulario version='compra' data={this.state} setData={setState} />
                <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Panel.Body>
        </Panel>)
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}