import React from 'react';
export default class Treeview extends React.Component<{ title: string, icon: string, state: string }, { open: boolean, height: number | string }> {
    state = { open: false, height: 44 };
    ul = React.createRef<HTMLUListElement>();
    render() {
        let { open, height } = this.state;
        return <li style={{ height, overflow: (this.props.state === 'open') ? 'hidden' : undefined }}
            onClick={this.collapse.bind(this)}
            className={"treeview " + (open) ? 'menu-open' : ''}>
            <a href="#menu">
                <i className={this.props.icon}></i>
                <span style={{ width: "230px" }}>{this.props.title}</span>
                <span className="pull-right-container">
                    <i className={"fa fa-angle-" + ((open) ? 'down' : 'left') + ' pull-right '}></i>
                </span>
            </a>
            <ul ref={this.ul} style={{ width: "230px", display: 'block' }} className="treeview-menu">
                {this.props.children}
            </ul>
        </li>
    }
    componentDidUpdate(p: any, s: any) {
        if (p.state === 'open') {
            if (this.state.open !== s.open) {
                let h = this.ul.current?.clientHeight;
                h = h ? h : 0;
                this.collapsed(h / 25, 25);
            }
            let { height } = this.state;
            if (height < 44)
                this.setState({ height: 44 });
        }
    }

    componentWillReceiveProps(p: any) {
        if (p.state === 'open') {
            this.setState({ height: 44 });
        } else {
            this.setState({ height: 'auto' });
        }
    }

    collapse() {
        this.setState({ open: !this.state.open });
    }
    collapsed(n: number = 0, i: number) {
        let { height, open } = this.state;
        setTimeout(() => {
            if (open) {
                this.setState({ height: height + n });
            } else {
                this.setState({ height: height - n });
            }
            if (i > 0)
                this.collapsed(n, i - 1);
        }, 0);
    }

}