import React from "react";
import { Button, ButtonGroup, Col, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import ReactTooltip from "react-tooltip";
import { conCotizacion, Cotizacion, sentCotizacion } from "../../Modelos/Factura";
import { condicionesNull, GeneralFila } from "../../Modelos/General";
import Condiciones from "../../Utils/Condiciones";
import { numberFormat } from "../../Utils/Utiles";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarCotizacion from "./BuscarCotizacion";
import BuscarGuardado from "./BuscarGuardado";
import AuxiliarCotizacion from "./Nuevo/AuxiliarCotizacion";
interface State extends Cotizacion {
    open: boolean
    cargar: boolean
    copiar: boolean
}
export default class NuevaCotizacion extends React.Component<any, State>{
    con = conCotizacion();
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...condicionesNull, cod_cliente: '', plantilla: 0, tasa: 0, dolar: false, copiar: false, open: false, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
                <ReactTooltip />
                {state.dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }} bsStyle="warning"
                    className="btn-block" onClick={() => { this.setState({ dolar: !state.dolar }) }} >
                    <span className="glyphicon glyphicon-usd" /> VISTA EN DOLARES
                </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }} bsStyle="primary"
                    className="btn-block" onClick={() => { this.setState({ dolar: !state.dolar }) }} >
                    <b>BS</b> VISTA EN BOLIVARES
                </Button>}
                {state.dolar ? <iframe src={"/api/Cotizacion/PDFD/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' /> :
                    <iframe src={"/api/Cotizacion/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />}
            </Panel>
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarCotizacion open={state.copiar} add={this.copiar.bind(this)} close={() => { this.setState({ copiar: false }) }} />
            <BuscarGuardado open={state.cargar} add={this.cargar.bind(this)} cargarCache={this.cargarCache.bind(this)} close={() => { this.setState({ cargar: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA COTIZACION</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Cliente' Cliente={state.cod_cliente} add={this.setProveedor.bind(this)} />
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>CONDICIONES</Button>
                <Col xs={4} style={{ textAlign: 'right' }} className="pull-right">
                    <b>TASA: {numberFormat(state.tasa)}</b>
                </Col>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Condiciones data={state} setData={this.setState} />
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarCotizacion key='auxCotizacion' Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "70px" }} xs={2}>
                        <Button onClick={() => { this.setState({ copiar: true }) }}
                            data-class="toolbody" data-html="true"
                            data-tip="COPIAR COTIZACION" bsStyle="primary">
                            <span className="glyphicon glyphicon-duplicate" />
                        </Button>
                    </Col>
                    <ButtonGroup>
                        <Button bsStyle="info" onClick={this.guardar.bind(this)} data-tip="GUARDAR">
                            <span className="glyphicon glyphicon-save-file"></span>
                        </Button>
                        <Button bsStyle="warning" onClick={() => { this.setState({ cargar: true }) }} data-tip="CARGAR">
                            <span className="glyphicon glyphicon-open-file" />
                        </Button>
                    </ButtonGroup>
                </AuxiliarCotizacion>
            </Panel.Body>
        </Panel>
    }
    copiar(d: GeneralFila) {
        this.con.detalle(d.codigo, data => {
            data.codigo = 0;
            data.tasa = this.state.tasa;
            this.setState(data)
        });
    }
    cargar(d: GeneralFila) {
        this.con.get('api/Cotizacion/cargar/' + d.codigo, data => {
            data.plantilla = data.codigo;
            data.tasa = this.state.tasa;
            data.codigo = 0;
            this.setState(data)
        })
    }
    cargarCache() {
        let dataCache = localStorage.getItem('guardarNuevaCotizacion');
        if (dataCache !== null) {
            let data = JSON.parse(dataCache);
            data.tasa = this.state.tasa;
            this.setState(data);

        }
    }
    guardar() {
        localStorage.setItem('guardarNuevaCotizacion', JSON.stringify(this.state));
        this.con.post('api/Cotizacion/guardar', sentCotizacion(this.state, this.impuesto), data => {
            this.setState({ plantilla: data });
            localStorage.setItem('guardarNuevaCotizacion', JSON.stringify(this.state));
            toast("DATOS GUARDADOS EN N¬∞ " + data, { type: toast.TYPE.SUCCESS })
        }, error => {
            toast("ERROR AL CONECTAR CON SEL SERVIDOR. GUARDADOS EN CACHE", { type: toast.TYPE.ERROR })
        });
    }
    componentDidMount() {
        this.con.get('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
        this.con.get('api/Dolares/valor', data => {
            this.setState({ tasa: data })
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_cliente: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentCotizacion(this.state, this.impuesto), data => {
                this.setState({ codigo: data })
            });
    }
}