import React from 'react';
import { Link } from 'react-router-dom';
import Treeview from './Treeview';

export default class Menu extends React.Component<{ state: string }> {
    render() {
        return <aside className="main-sidebar">
            <section className="sidebar">
                <ul className="sidebar-menu">
                    <li className="active">
                        <a href="/panel_ad">
                            <i className="fa fa-dashboard"></i> <span>Inicio</span>
                        </a>
                    </li>
                    <li>
                        <Link to="/ver_ordenes">
                            <i className="fa fa-fw fa fa-paperclip"></i> <span style={{ width: "200px" }}> ORDENES DE COMPRA </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_compras">
                            <i className="fa fa-fw fa fa-shopping-cart"></i> <span> COMPRAS </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_cotizaciones">
                            <i className="fa fa-fw fa fa-calculator"></i> <span> COTIZACIONES </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_notas">
                            <i className="fa fa-fw fa fa-send"></i> <span> NOTAS DE ENTREGA </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_facturas">
                            <i className="fa fa-fw fa fa-usd"></i> <span> FACTURAS </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_proveedores">
                            <i className="fa fa-fw fa fa-truck"></i> <span> PROVEEDORES</span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/ver_clientes">
                            <i className="fa fa-fw fa fa-user"></i><span> CLIENTES</span>
                        </Link>
                    </li>
                    <Treeview state={this.props.state} title='INVENTARIO' icon='fa fa-fw fa fa-laptop'>
                        <li>
                            <Link to="ver_productos">
                                <i className="fa fa-circle-o"></i> Consulta de inventario
                            </Link>
                        </li>
                        <li>
                            <a href="nuevo_producto">
                                <i className="fa fa-circle-o"></i> Registro de productos
                                </a>
                        </li>
                        <li>
                            <a href="ajuste_inventario">
                                <i className="fa fa-circle-o"></i> Ajuste de inventario
                                </a>
                        </li>
                        <li>
                            <a href="ajuste_utilidad">
                                <i className="fa fa-circle-o"></i> Ajuste de utilidad
                                </a>
                        </li>
                        <li>
                            <a href="ajuste_precio">
                                <i className="fa fa-circle-o"></i> Ajuste de Precio
                                </a>
                        </li>
                    </Treeview>
                    <Treeview state={this.props.state} title='REPORTES' icon='fa fa-fw fa-bar-chart'>
                        <li>
                            <a href="reporte_compras"><i className="fa fa-circle-o"></i> Reporte de compras</a>
                        </li>
                        <li>
                            <a href="reporte_ventas"><i className="fa fa-circle-o"></i> Reporte de ventas</a>
                        </li>
                        <li>
                            <a href="reporte_vendedor"><i className="fa fa-circle-o"></i> Reporte de vendedores</a>
                        </li>
                        <li>
                            <a href="reporte_ajustes"><i className="fa fa-circle-o"></i> Reporte de ajustes de inventario</a>
                        </li>
                        <li>
                            <a href="reporte_inventario"><i className="fa fa-circle-o"></i> Reporte de inventario</a>
                        </li>
                        <li>
                            <a href="rubros"><i className="fa fa-circle-o"></i> Reporte de rubros por mes</a>
                        </li>
                        <li>
                            <a href="seguimiento"><i className="fa fa-circle-o"></i> Seguimiento de compras</a>
                        </li>
                        <li>
                            <a href="historico"><i className="fa fa-circle-o"></i> historico de productos</a>
                        </li>
                    </Treeview>
                    <li>
                        <a href="/ver_usuarios">
                            <i className="fa fa-fw fa fa-users"></i><span> USUARIOS</span>
                        </a>
                    </li>
                    <li>
                        <Link to="/conf_ventas">
                            <i className="fa fa-fw fa-wrench"></i><span> CONFIGURACION</span>
                        </Link>
                    </li>
                </ul>
            </section>
        </aside>
    }
}