import React from 'react';
import { Button, Col, Panel } from 'react-bootstrap';
import { ConConfig } from '../../Modelos/Config';
import InputText from '../../Utils/InputText';
import SetText from '../../Utils/setTex';
interface State {
    nombre: string
    numero_fiscal: string
    direccion: string
    telefono: string
    correo: string
    web: string
    pago: string
    logo: string
    eslogan: string
}
export default class Empresa extends React.Component<any, State> {
    con = ConConfig('empresa');
    state = { nombre: '', numero_fiscal: '', direccion: '', telefono: '', correo: '', web: '', pago: '', logo: '', eslogan: '' };
    render() {
        let { nombre, numero_fiscal, direccion, telefono, correo, web, pago, logo, eslogan } = this.state;
        let img = logo.length < 100 ? '/public/imagenes/' + logo : logo;
        let setState = this.setState.bind(this);
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE REGION </h4>
            </Panel.Heading>
            <Panel.Body>
                <Col md={12}>
                    <InputText label="NUMERO FISCAL" widthLabel={150} value={numero_fiscal} setData={setState} update="numero_fiscal" />
                </Col>
                <Col md={12}><br />
                    <InputText label="NOMBRE" widthLabel={100} value={nombre} setData={setState} update="nombre" />
                </Col>
                <Col md={12}><br />
                    <InputText label="CORREO" widthLabel={100} value={correo} setData={setState} update="correo" />
                </Col>
                <Col md={12}><br />
                    <InputText label="TELEFONO" widthLabel={120} value={telefono} setData={setState} update="telefono" />
                </Col>
                <Col md={12}><br />
                    <InputText label="WEB" widthLabel={60} value={web} setData={setState} update="web" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">DIRECCION:</label>
                    <SetText rows={3} type="textarea" setData={this.setState.bind(this)} value={direccion} update="direccion" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">INFORMACION DE PAGO:</label>
                    <SetText rows={3} type="textarea" setData={this.setState.bind(this)} value={pago} update="pago" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">ESLOGAN:</label>
                    <SetText rows={3} type="textarea" setData={this.setState.bind(this)} value={eslogan} update="eslogan" />
                </Col>
                <Col md={12}><br />
                    <label className="control-label">LOGO:</label>
                    <label style={{ width: '300px' }}>
                        <img src={img} width="100%" alt='logo' />
                        <input style={{ display: "none" }} onChange={this.setImage.bind(this)} type="file" />
                    </label>
                </Col>
                <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Panel.Body>
        </Panel>)
    }
    setImage(e: React.ChangeEvent<HTMLInputElement>) {
        let t = this, f = e.currentTarget.files;
        let reader = new FileReader()
        reader.onloadend = function () {
            t.setState({ logo: reader.result as string })
        }
        if (f != null) reader.readAsDataURL(f[0]);
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}