import React from 'react';
import { Button, InputGroup, Panel } from 'react-bootstrap';
import { ConConfig } from '../../../Modelos/Config';
import InputText from '../../../Utils/InputText';
import SetText from '../../../Utils/setTex';
import Formulario from './Formulario';
import { Impreciones, imprecionNull } from '../../../Modelos/General';
interface State extends Impreciones{
    observacion: string,
    num_factura: number,
}
export default class Factura extends React.Component<any, State> {
    con = ConConfig('factura');
    state = { ...imprecionNull, num_factura: 0, observacion: '', };
    render() {
        let { num_factura, observacion } = this.state;
        let setState = this.setState.bind(this);
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE FACTURA </h4>
            </Panel.Heading>
            <Panel.Body>
                <Panel>
                    <Panel.Heading>
                        <h5>CONFIGURACION DE DATOS POR DEFECTO </h5>
                    </Panel.Heading>
                    <Panel.Body>
                        {(num_factura > 0) ?
                            <InputGroup style={{ width: '100%' }} className="mb-3">
                                <span style={{ width: '300px', position: 'relative', backgroundColor: "#eee" }} className="form-control">CORREO</span>
                                <span style={{ width: 'calc(100% - 300px)', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }} className="form-control">{num_factura}</span>
                            </InputGroup> :
                            <InputText value={num_factura} setData={setState} update="num_factura"
                                label="NUMERO INICIAL DE FACTURA" widthLabel={300} minValue={0} step="1" type="number" />}
                        <b style={{ color: "red" }}>UNA VEZ INICIALIZADO YA NO PUEDE SER CAMBIADO</b>
                        <br /><b>OBSERVACI√ìN</b>
                        <SetText rows={6} maxLength={600} type="textarea" value={observacion} setData={setState} update="observacion" />
                    </Panel.Body>
                </Panel>
                <Formulario version='factura' data={this.state} setData={setState} />
                <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Panel.Body>
        </Panel >)
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}