import React from "react";
import { Button, Col, Panel, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { conOrden, Orden, ordenNull, sentOrden } from "../../Modelos/Compra";
import { GeneralFila } from "../../Modelos/General";
import CacheStorage from "../../Utils/CacheStorage";
import Condiciones from "../../Utils/Condiciones";
import ClienteSelector from "../Clientes/ClienteSelector";
import AuxiliarCompra from "../Compra/Nuevo/AuxiliarCompra";
import BuscarOrden from "./BuscarOrden";
interface State extends Orden {
    open: boolean
    cargar: boolean
}
export default class NuevaOrden extends React.Component<any, State>{
    con = conOrden();
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...ordenNull, open: false, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Orden/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <BuscarOrden open={state.cargar} add={this.cargar.bind(this)} close={() => { this.setState({ cargar: false }) }} />
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> NUEVA ORDEN DE COMPRA</h4>
            </Panel.Heading>
            <Panel.Body>
                <ClienteSelector type='Proveedor' Cliente={state.cod_proveedor} add={this.setProveedor.bind(this)} />
                <Button style={{ marginBottom: '15px' }}
                    onClick={() => { this.setState({ open: !this.state.open }) }}
                    bsStyle='primary'>CONDICIONES</Button>
                <Panel.Collapse>
                    <Panel bsStyle="info">
                        <Panel.Body>
                            <Row>
                                <Condiciones data={state} setData={this.setState} />
                            </Row>
                        </Panel.Body>
                    </Panel>
                </Panel.Collapse>
                <AuxiliarCompra key='auxOrden' Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} >
                    <Col style={{ width: "70px" }} xs={2}>
                        <Button onClick={() => { this.setState({ cargar: true }) }}
                            data-class="toolbody" data-html="true"
                            data-tip="COPIAR ORDEN <br> DE COMPRA" bsStyle="primary">
                            <span className="glyphicon glyphicon-duplicate" />
                        </Button>
                    </Col>
                    <CacheStorage data={this.props.data} setData={this.props.setData} Identificador='NuevaOrden' />
                </AuxiliarCompra>
            </Panel.Body>
        </Panel>
    }
    cargar(d: GeneralFila) {
        this.con.get('api/Orden/detalles/' + d.codigo, data => {
            data.codigo = 0;
            this.setState(data);
        })
    }
    componentDidMount() {
        this.con.get('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_proveedor: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_proveedor === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentOrden(this.state, this.impuesto),
                data => { this.setState({ codigo: data }) });
    }
}