import React from 'react';
import Tabla from '../../Utils/Table';
import { conMoneda, Moneda } from '../../Modelos/General';
import { FilaMoneda } from './Fila';
import { Button } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import NuevoMoneda from './Nuevo';
export default class ListMoneda extends Tabla {
    con = conMoneda();
    mostrar = 6;
    placeholder = "BUSCAR MONEDA";
    estatus = [{ value: 0, label: 'CANCELADO' }, { value: 1, label: 'SIN FACTURA' }, { value: 2, label: 'FACTURADA' }];
    buscarLabel: any = ["codigo", "descripcion"];
    title() {
        return <h4><i className="glyphicon glyphicon-search"></i> BUSCAR MONEDA</h4>
    }
    Avansado() {
        return <></>;
    }
    renderBusqueda() {
        return <>
            <div className="input-group col-md-12">
                <ReactTooltip />
                <input style={{ position: 'relative', width: "50%" }} onKeyUp={this.setBuscar.bind(this)} name="buscar" placeholder={this.placeholder} className="form-control" />
                <div className="input-group-append">
                    <Button style={{ width: '100px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="primary">
                        <span className="glyphicon glyphicon-search"></span> BUSCAR
                    </Button>
                </div>
            </div>
            <NuevoMoneda update={this.set.bind(this)} />
        </>
    }
    Header() {
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>CODIGO {this.flechas("codigo")}</th>
            <th style={{ width: "calc(100% - 270)", backgroundColor: this.colorOrden("descripcion") }} onClick={() => { this.setOrden("descripcion") }}>DESCRIPCION {this.flechas("descripcion")}</th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "70px" }}>ACCIONES</th>
        </tr>
    }
    Row(p: Moneda) {
        return <FilaMoneda d={p} update={this.set.bind(this)} />
    }
}
