import React from 'react';
import { Button, ButtonGroup, Col, Panel } from 'react-bootstrap';
import { ConConfig } from '../../Modelos/Config';
interface State {
    costo: number
    tasa: boolean
}
export default class Costos extends React.Component<any, State> {
    con = ConConfig('costo');
    state = { costo: 1, tasa: false };
    render() {
        let { costo, tasa } = this.state;
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE COSTOS </h4>
            </Panel.Heading>
            <Panel.Body>
                <Col md={12}>
                    <b> ACTUALIZAR COSTOS DE LOS PRODUCTOS: </b>
                    <p>INDICA COMO EL SISTEMA MANEJARA EL COSTO DE SUS PRODUCTOS</p>
                    <ButtonGroup vertical block>
                        <Button onClick={() => { this.setState({ costo: 1 }) }} bsStyle={(costo === 1) ? 'success' : 'default'}>
                            REGISTRARA EL COSTO FINAL A LA ULTIMA COMPRA REALIZADA
                        </Button>
                        <Button onClick={() => { this.setState({ costo: 2 }) }} bsStyle={(costo === 2) ? 'success' : 'default'}>
                            MANTENDRA COMO COSTO EL MAYOR EN CUALQUIER COMPRA REALIZADA
                        </Button>
                        <Button onClick={() => { this.setState({ costo: 3 }) }} bsStyle={(costo === 3) ? 'success' : 'default'}>
                            EL COSTO NO SE VE AFECTADO POR LA COMPRA
                        </Button>
                    </ButtonGroup>
                </Col>
                <Col md={12}><br />
                    <b> COSTO VASADO REFERENCIA DE LA TASA CAMBIARIA:</b>
                    <p>SI SE MANEJA EN BOLIVARES SE USARA EL COSTO EN REGISTRADO EN BOLIVARES</p>
                    <p>SI SE MANEJA EN DOLARES SE USARA EL COSTO EN REGISTRADO EN DOLARES TRANSFORMADO A BOLIVARES DE LA TASA DEL DIA DE LA OPERACION</p>
                    <ButtonGroup style={{ width: '100%' }}>
                        <Button style={{ width: '50%' }} onClick={() => { this.setState({ tasa: false }) }} bsStyle={!tasa ? 'success' : 'default'}>
                            BOLIVARES
                        </Button>
                        <Button style={{ width: '50%' }} onClick={() => { this.setState({ tasa: true }) }} bsStyle={tasa ? 'success' : 'default'}>
                            DOLARES
                        </Button>
                    </ButtonGroup>
                </Col>
                <Col md={12}><br />
                    <Button bsStyle="success">
                        <i className="glyphicon glyphicon-ok-circle"></i>
                    </Button> LAS QUE ESTEN DE VERDE SON QUE ESTAN ACTIVAS
                </Col>
                <Col md={12}><br />
                    <p><b>ACTUALIZAR COSTOS DE LOS PRODUCTOS </b> SE REFIERE A CUANDO SE ACTUALIZARA EL COSTO DURANTE UNA COMPRA</p>
                </Col>
                <Col md={12}><br />
                    <p><b>COSTO DE REFERENCIA </b> SE REFIERE AL COSTO QUE USARA COMO BASE PARA LOS CALCULO DE:</p>
                    <p>1) COMPROBAR SI EL PRECIO ES MAYOR AL REGISTRADO</p>
                    <p>2) CALCULAR LOS PRECIOS 1, 2, Y 3</p>
                    <p>3) EL COMO SE TE PRESENTARA LA COTIZACION AL MOMENTO DE CREARLA (SI ESTARA EN DOLARES O NO)</p>
                    <p>NOTA: LA PRESENTACION DE LA COTIZACION SE PUEDE CAMBIAR DURANTE LA CREACION DE LA MISMA</p>
                </Col>
                <Col md={12}><br />
                    <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                        <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                    </Button>
                </Col>
            </Panel.Body>
        </Panel>)
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}