import React from 'react';
import { Button, Panel } from 'react-bootstrap';
import { ConConfig } from '../../../Modelos/Config';
import Formulario from './Formulario';
import { Impreciones, imprecionNull } from '../../../Modelos/General';
import Condiciones from '../../../Utils/Condiciones';
interface State extends Impreciones {
    nota: string,
    forma_pago: string
    tiempo_entrega: string
    validez: string
}
export default class Cotizacion extends React.Component<any, State> {
    con = ConConfig('cotizacion');
    state = { ...imprecionNull, nota: '', forma_pago: '', tiempo_entrega: '', validez: '' };
    render() {
        let setState = this.setState.bind(this);
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE COTIZACION </h4>
            </Panel.Heading>
            <Panel.Body>
                <Panel>
                    <Panel.Heading>
                        <h5>CONFIGURACION DE DATOS POR DEFECTO </h5>
                    </Panel.Heading>
                    <Panel.Body>
                        <Condiciones data={this.state} setData={setState} />
                    </Panel.Body>
                </Panel>
                <Formulario version='cotizacion' data={this.state} setData={setState} />
                <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Panel.Body>
        </Panel>)
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}