import React from 'react'
import { Button, Modal, Row, Col, InputGroup, FormControl } from 'react-bootstrap'
import { conDepartamento, conProducto, conTipo, Producto } from '../../Modelos/Productos'
import { numberFormat } from '../../Utils/Utiles'
import SetText from '../../Utils/setTex'
import UnidadesSelector from '../Unidad/Selector'
import { toast } from 'react-toastify'
interface Props {
    open: boolean
    close: () => void
    add: (p: Producto) => void
    producto: Producto;
}
interface State extends Producto {
    cateName?: string
}
export default class EditProductos extends React.Component<Props, State>{
    con = conProducto();
    departamento = '';
    tipo = '';
    constructor(p: Props) {
        super(p)
        this.state = p.producto;
        this.setState = this.setState.bind(this)
    }

    setUnidad(unidad: number) {
        this.setState({ unidad })
    }

    render() {
        let { setState, state: { descripcion, codigo, unidad, precio1, precio2, precio3, costo }, props: { open, close }, tipo, departamento } = this;
        return <Modal bsSize="lg" animation show={open} onHide={close}>
            <Modal.Header closeButton>
                <h4><i className="glyphicon glyphicon-edit"></i> EDITAR {descripcion}</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col xs={4}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">CODIGO</span>
                            <span style={{ width: 'calc(100% - 100px)', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">{codigo}</span>
                        </InputGroup>
                    </Col>
                    <Col xs={8}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '150px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">DEPARTAMENTO</span>
                            <span style={{ width: 'calc(100% - 150px)', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">{departamento}</span>
                        </InputGroup>
                    </Col>
                    <Col xs={12}><br />
                        <b>DESCRIPCI√ìN</b>
                        <SetText type="textarea" rows={3} maxLength={300} value={descripcion} setData={setState} update="descripcion" />
                    </Col>
                    <Col xs={6}><br />
                        <UnidadesSelector defaultValue={unidad} change={this.setUnidad.bind(this)} />
                    </Col>
                    <Col xs={6}><br />
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '180px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">TIPO DE PRODUCTO</span>
                            <span style={{ width: 'calc(100% - 180px)', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">{tipo}</span>
                        </InputGroup>
                    </Col>

                    <Col xs={12}><br />
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">COSTO</span>
                            <FormControl style={{ width: 'calc(100% - 100px)', textAlign: 'right' }} disabled value={numberFormat(costo)} />
                        </InputGroup>
                    </Col>
                    {/* utilidades de venta */}
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '120px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">UTILIDAD 1</span>
                            <FormControl style={{ width: 'calc(100% - 120px)', textAlign: 'right' }} disabled value={numberFormat(precio1)} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '120px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">UTILIDAD 2</span>
                            <FormControl style={{ width: 'calc(100% - 120px)', textAlign: 'right' }} disabled value={numberFormat(precio2)} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '120px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">UTILIDAD 3</span>
                            <FormControl style={{ width: 'calc(100% - 120px)', textAlign: 'right' }} disabled value={numberFormat(precio3)} />
                        </InputGroup>
                    </Col>
                    {/* precios de venta */}
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 1</span>
                            <FormControl style={{ width: 'calc(100% - 100px)', textAlign: 'right' }} disabled value={numberFormat(this.getPrecio(precio1))} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 2</span>
                            <FormControl style={{ width: 'calc(100% - 100px)', textAlign: 'right' }} disabled value={numberFormat(this.getPrecio(precio2))} />
                        </InputGroup>
                    </Col>
                    <Col xs={4}><br />
                        <InputGroup className="mb-3">
                            <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }}
                                className="form-control">PRECIO 3</span>
                            <FormControl style={{ width: 'calc(100% - 100px)', textAlign: 'right' }} disabled value={numberFormat(this.getPrecio(precio3))} />
                        </InputGroup>
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={this.actualizar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Modal.Footer>
        </Modal>
    }
    actualizar() {
        let { descripcion } = this.state;
        if (descripcion === '') {
            toast('debe colocar la una descripcion', { type: toast.TYPE.ERROR })
        } else {
            this.con.actualizar(this.state.codigo, this.state,
                data => { this.props.add(data); this.props.close(); });
        }
    }
    getPrecio(price: number) {
        let utilidad = (price / 100) + 1
        return this.state.costo * utilidad
    }

    componentDidMount() {
        let { departamento, tipo } = this.props.producto;
        conDepartamento().detalle(departamento, data => {
            this.departamento = data.descripcion; this.forceUpdate();
        });
        conTipo().detalle(tipo, data => {
            this.tipo = data.descripcion; this.forceUpdate();
        });
    }
}
