import React from 'react';
import { Button, ButtonGroup, Col, InputGroup, Panel } from 'react-bootstrap';
import InputText from '../../../Utils/InputText';
import { Impreciones } from '../../../Modelos/General';
import { ConConfig } from '../../../Modelos/Config';
interface Props {
    data: Impreciones
    version: string
    setData: (d: any) => void
}
export default class Formulario extends React.Component<Props, { verciones: { value: 1, nombre: string }[] }> {
    state: { verciones: { value: 1, nombre: string }[] } = { verciones: [] };
    render() {
        let { data: { margen_sup, margen_inf, margen_izq, margen_der, tipo_papel, encabezado, piepagina, version }, setData } = this.props;
        return (<Panel>
            <Panel.Heading>
                <h5>CONFIGURACION DE IMPRESI√ìN </h5>
            </Panel.Heading>
            <Panel.Body>
                <Col xs={12}>
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <span style={{ width: '200px', position: 'relative', backgroundColor: "#eee" }} className="form-control">TIPO DE PAGINA</span>
                        <select style={{ width: 'calc(100% - 200px)', position: 'relative' }} onChange={this.setTipoPapel.bind(this)}
                            className="form-control" value={tipo_papel}>
                            <option value="LETTER">CARTA</option>
                            <option value="LEGAL">OFICIO</option>
                        </select>
                    </InputGroup>
                </Col>
                {(this.state.verciones.length > 0) ? <Col xs={12}><br />
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }} className="form-control">VERSION</span>
                        <select style={{ width: 'calc(100% - 100px)', position: 'relative' }} onChange={this.setVersion.bind(this)}
                            className="form-control" value={version}>
                            {this.state.verciones.map(v => {
                                return <option value={v.value}>{v.nombre}</option>
                            })}
                        </select>
                    </InputGroup>
                </Col> : <></>}
                <Col md={12}><br />
                    <ButtonGroup style={{ width: '100%' }}>
                        <Button style={{ width: '50%' }} onClick={() => { setData({ encabezado: !encabezado }) }} bsStyle={encabezado ? 'success' : 'danger'}>
                            COLOCAR ENCABEZADO: {encabezado ? 'SI' : 'NO'}
                        </Button>
                        <Button style={{ width: '50%' }} onClick={() => { setData({ piepagina: !piepagina }) }} bsStyle={piepagina ? 'success' : 'danger'}>
                            COLOCAR PIE DE PAGINA: {piepagina ? 'SI' : 'NO'}
                        </Button>
                    </ButtonGroup>
                </Col>
                <Col md={6}><br />
                    <InputText minValue={1} step="1" type="number" label="MARGEN SUPERIOR" widthLabel={200} value={margen_sup} setData={setData} update="margen_sup" />
                </Col>
                <Col md={6}><br />
                    <InputText minValue={1} step="1" type="number" label="MARGEN INFERIOR" widthLabel={200} value={margen_inf} setData={setData} update="margen_inf" />
                </Col>
                <Col md={6}><br />
                    <InputText minValue={1} step="1" type="number" label="MARGEN IZQUIERDO" widthLabel={200} value={margen_izq} setData={setData} update="margen_izq" />
                </Col>
                <Col md={6}><br />
                    <InputText minValue={1} step="1" type="number" label="MARGEN DERECHO" widthLabel={200} value={margen_der} setData={setData} update="margen_der" />
                </Col>
            </Panel.Body>
        </Panel>)
    }
    setTipoPapel(e: React.ChangeEvent<HTMLSelectElement>) {
        let value = e.currentTarget.value as string;
        this.props.setData({ tipo_papel: value });
    }
    setVersion(e: React.ChangeEvent<HTMLSelectElement>) {
        let value = e.currentTarget.value as string;
        this.props.setData({ version: value });
    }
    componentDidMount() {
        ConConfig('versiones').pull(data => {
            console.log(data[this.props.version]);
            this.setState({ verciones: data[this.props.version] })
            this.forceUpdate()
        })
    }
}