import React from 'react';
import { Button, Col, InputGroup, Panel } from 'react-bootstrap';
import { ConConfig } from '../../Modelos/Config';
import InputText from '../../Utils/InputText';
import MonedaSelector from '../Moneda/Selector';
interface State {
    cod_fiscal: string,
    moneda: string
    impuesto: number
}
export default class Region extends React.Component<any, State> {
    con = ConConfig('region');
    state = { cod_fiscal: '', moneda: '', impuesto: 0 };
    render() {
        let { cod_fiscal, moneda, impuesto } = this.state;
        let setState = this.setState.bind(this);
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE REGION </h4>
            </Panel.Heading>
            <Panel.Body>
                <Col md={12}>
                    <InputText after="EJEMPLO: RIF, CUIT, NIT..." widthAfter={250} label="IDENTIFICACION TRIBUTARIA" widthLabel={250} value={cod_fiscal} setData={setState} update="cod_fiscal" />
                </Col>
                <Col md={7}><br />
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <span style={{ width: '100px', position: 'relative', backgroundColor: "#eee" }} className="form-control">MONEDA</span>
                        <MonedaSelector defaultValue={moneda} change={this.setMoneda.bind(this)} />
                    </InputGroup>
                </Col>
                <Col md={5}><br />
                    <InputText minValue={1} step="0.01" type="number" label="IMPUESTO" widthLabel={100} value={impuesto} setData={setState} update="impuesto" />
                </Col>
                <Col md={12}><br />
                    <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                        <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                    </Button>
                </Col>
            </Panel.Body>
        </Panel>)
    }
    setMoneda(moneda: string) {
        this.setState({ moneda })
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}