
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { conCotizacion } from '../../Modelos/Factura';
import FilaProto from '../../Utils/FilaProto';
import ModalPrint from '../../Utils/ModalPrint';
import Seguimientos from '../../Utils/Seguimiento';
import { zfill } from '../../Utils/Utiles';
import ModalDetalle from './ModalDetalle';
import PrintDolar from './ModalPrint';

export default class FilaCotizacio extends FilaProto {
    con=conCotizacion();
    constructor(p: any) {
        super(p);
        this.Label = 'COTIZACI√ìN';
        this.tipo = 'Cotizacion';
    }
    getModal() {
        let { codigo, tasa } = this.props.data;
        return <>
            {(tasa > 0) ? <PrintDolar title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
                codigo={zfill(codigo)}
                open={this.state.print} close={this.close.bind(this)} /> : <ModalPrint title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
                    url={"/api/" + this.tipo + "/PDF/" + codigo}
                    open={this.state.print} close={this.close.bind(this)} />}
            <Seguimientos
                type='Cotizacion'
                id={codigo}
                close={this.close.bind(this)}
                open={this.state.aux}
            />
            <ModalDetalle
                titulo={"DETALLES DE LA COTIZACI√ìN N¬∞ " + zfill(codigo)}
                id={codigo}
                open={this.state.detalles}
                close={this.close.bind(this)} />
        </>
    }
    getStatus() {
        switch (this.props.data.status) {
            case 1:
                return <span className="label label-warning">PENDIENTE</span>
            case 2:
                return <span className="label label-success">PROCESADA</span>
            default:
                return <span className="label label-danger">CANCELADA</span>
        }
    }
    getButtons() {
        return <ButtonGroup className="pull-right">
            <Button data-tip="VER SEGUIMIENTO"
                onClick={() => { this.setState({ aux: true }) }} >
                <i className="glyphicon glyphicon-eye-open"></i>
            </Button>
            <Button data-tip="VER IMPRECION" bsStyle="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="glyphicon glyphicon-download"></i>
            </Button>
            {(this.props.data.status === 1) ?
                <Button data-tip="CANCELAR" bsStyle="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="glyphicon glyphicon-ban-circle"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
}