import React from 'react'
import { Button, Col, Row } from 'react-bootstrap';
import Cliente, { conCliente } from '../../Modelos/Cliente'
import Tabla from '../../Utils/Table'
import { busqueda, filtro, ordenarPor } from '../../Utils/Utiles';
import FilaCliente from './Fila'
import FormCliente from './FormCliente';

export default class ListClientes extends Tabla {
    con = conCliente();
    placeholder = "BUSCAR CLIENTE";
    buscarLabel: any[] = ['codigo', 'nombre', 'contacto', 'correo', 'telefono'];
    Avansado() {
        return <Row>
            <Col style={{ minWidth: '230px' }} md={12}><br />
                <select data-tip="FILTRAR POR EL ESTADO" onChange={this.setStatus.bind(this)} className="form-control" value={this.state.estatus}>
                    <option value={-1}>TODOS LOS ESTADOS</option>
                    <option value={0}>INACTIVO</option>
                    <option value={1}>ACTIVO</option>
                </select>
            </Col>
        </Row>;
    }
    title() {
        return <>
            <FormCliente open={this.state.modalProductos} title='NUEVO CLIENTE' type='Cliente'
                cliente={null} close={() => { this.setState({ modalProductos: false }) }} add={this.set.bind(this)} />
            <div className="btn-group pull-right">
                <Button onClick={() => { this.setState({ modalProductos: true }) }} bsStyle='primary'>
                    <span className="glyphicon glyphicon-plus"></span> NUEVO CLIENTE
            </Button>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> BUSCAR CLIENTE</h4>
        </>
    }
    Header() {
        return <tr className="info">
            <th style={{ width: "100px", backgroundColor: this.colorOrden("codigo") }}
                onClick={() => { this.setOrden("codigo") }}>
                RIF {this.flechas("codigo")}
            </th>
            <th style={{ width: 'calc(100% - 335px)', backgroundColor: this.colorOrden("nombre") }}
                onClick={() => { this.setOrden("nombre") }}>
                RASON SOCIAL {this.flechas("nombre")}
            </th>
            <th style={{ width: "100px" }}>ESTATUS</th>
            <th style={{ width: "135px" }}>ACCIONES</th>
        </tr>
    }
    Row(d: Cliente) {
        return <FilaCliente type='Cliente' key={d.codigo + d.estatus}
            update={this.set.bind(this)} data={d} />
    }
    filtros() {
        let { itens, buscar, estatus, ordenar } = this.state;
        if (itens !== 'carro') {
            itens = busqueda(itens, buscar, this.buscarLabel);
            itens = filtro(itens, estatus, 'estatus');
            itens = ordenarPor(itens, this.orden, ordenar);
            this.length = itens.length;
            return itens;
        }
        return [];
    }
}
