import { Conexion } from "../Config/Coneccion";

export interface UsuarioFila {
    codigo: number,
    nombre: string,
    correo: string,
    clave: string
}

class UsuarioConexion extends Conexion {
    clase = 'Usuario';
    persistencia = 1000000;
}
export function conUsuario() {
    return new UsuarioConexion();
}