import React from 'react';
import { Button, Panel } from 'react-bootstrap';
import { ConConfig } from '../../Modelos/Config';
import SetText from '../../Utils/setTex';
interface State {
    garantia: string,
    observacion: string
}
export default class Ventas extends React.Component<any, State> {
    con = ConConfig('ventas');
    state = { observacion: '', garantia: '' };
    render() {
        return (<Panel bsStyle='info'>
            <Panel.Heading>
                <h4><i className="glyphicon glyphicon-edit"></i> CONFIGURACION DE VENTAS </h4>
            </Panel.Heading>
            <Panel.Body>
                <label className="control-label">GARANTIA DE LOS PRODUCTOS:</label>
                <SetText type="textarea" setData={this.setState.bind(this)} value={this.state.garantia} update="garantia" />
                <br /><label className="control-label">OBSERVACION GENERAL:</label>
                <SetText type="textarea" setData={this.setState.bind(this)} value={this.state.observacion} update="observacion" />
                <br />
                <Button className="pull-right" onClick={this.guardar.bind(this)} bsStyle="primary">
                    <span className="glyphicon glyphicon-floppy-disk" /> Guardar
                </Button>
            </Panel.Body>
        </Panel>)
    }
    componentDidMount() {
        this.con.pull(this.setState.bind(this));
    }
    guardar() {
        this.con.push(this.state, this.setState.bind(this));
    }
}